body{
    background-color: aqua;
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100vh;
}
.green{
    background-color: green;
}
.red{
    background-color: red;
}
.setupform{
   width: 350px;
    height: auto;
    background-color: cornsilk;
    display: flex;
    flex-direction: column;
    padding: 20px;
    border-radius: 10px;

}
.form-control{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    width: 80%;
    
}

.start-quiz{
    background-color: green;
    color: honeydew;
    border:  none;
    width: 100%;
    height: 30px;
    margin-top: 20px;
}
.answers{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

}
.answers>button{
    margin: 5px;
    height: auto;
    width: 80%;
}
.main{
    width: 350px;
}

